.menu-item-box a{
    /* "Blippo", "OCR A Std", monospace */
    font: bold 20px monospace;
    text-decoration: none;
    color: white;
    transition: color 1s cubic-bezier(0.32, 0, 0.67, 0);
}
.menu-item-box a:hover{
    color: #e8fd26;
    font: bold 18px Tahoma;
    transition: color 1s cubic-bezier(0.33, 1, 0.68, 1);
}
.menu-item-box a::before {
    content: "";
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    -webkit-clip-path: polygon(
        0% -20%,
        100% -30%,
        100% -10%,
        0% 0%,
        0% 130%,
        100% 120%,
        100% 100%,
        0% 110%
    );
            clip-path: polygon(
        0% -20%,
        100% -30%,
        100% -10%,
        0% 0%,
        0% 130%,
        100% 120%,
        100% 100%,
        0% 110%
    );
    background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);
    transition: -webkit-clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    transition: clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    transition: clip-path 1s cubic-bezier(0.25, 1, 0.5, 1), -webkit-clip-path 1s cubic-bezier(0.25, 1, 0.5, 1);
    opacity: 0.5;

}

.menu-item-box a:hover::before {
    -webkit-clip-path: polygon(
        0% 10%,
        100% 0%,
        100% 20%,
        0% 30%,
        0% 100%,
        100% 90%,
        100% 70%,
        0% 80%
    );
            clip-path: polygon(
        0% 10%,
        100% 0%,
        100% 20%,
        0% 30%,
        0% 100%,
        100% 90%,
        100% 70%,
        0% 80%
    );
}

