.swiper-container{
    position: relative;
    width: 100vw;
    background-color: black;
    opacity: 0.05;
    margin-left: 0px;
    padding-left: -50px;
    margin-top: 5vh;
    margin-bottom: 5vh;
    transition-property: transform;
    transition-timing-function: ease-out;
    transform: translate3d(0px, 0px, 0px);
}